.cards{
    display: flex;
    flex-direction: column;
    border: solid black 1px;
    width: 200px;
    height: 300px;
    margin: 10px 20px;
    border-radius: 5px;
}
.cards:hover{
    scale: 1.1;
}
.cartimage{
    display: flex;
    height: 100%;
    width: auto;
}

.cartimage img{
width: 100%;
   
}
.allcards{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
 
}

.overallmain{
    
    width: 80%;
}

.fullpage{
    display: flex;

}
.forcenter{
    width: 100%;
    display: flex;
  
    flex-direction: row;
    justify-content: center;
}
.forcenter table{
    position: fixed;
}
.width-100{
    width: 30%;
    overflow: scroll;
}


.topcart{
    display: flex;
    justify-content: space-between;
}
.topcart div {
    border: solid black;
    border-radius: 2px;
}
.details{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.name{
    color: green;
}
.price{
    color: red;
}
.addto{
    width: 90%;
    padding: 4px;
    margin: 2px;
    background-color: rgb(237, 115, 70);
    border-radius: 5px;
}
.hideclass{
    display: none;
}

.topcart table th{
    border: solid red;
}

/* Apply basic styles to the table */
table {
    width: 100%;
    border-collapse: collapse;
    border: 2px solid black;
  }
  
  /* Style the table header */
  thead th {
    background-color: #f2f2f2;
    border: 2px solid black;
    padding: 8px;
    text-align: left;
  }
  
  /* Style the table rows */
  tbody td {
    border: 2px solid black;
    padding: 8px;
    text-align: left;
  }
  
  /* Add a hover effect on table rows */
  tbody tr:hover {
    background-color: #f5f5f5;
  }
 .cartIcon{
    display: none;
 }
  
  
  @media(max-width:770px){
    .width-100{
       
        display: none;
    }
    .overallmain{

        width: 100%;
    }
    .fullpage{
        position: relative;;
    }
    .cartIcon{
        position: absolute;
        display: block;
        top: 20px;
        right: 30px;
    }
   
    }
    .cartVisible{
        display: block;
        width: 100%;
        position: absolute;
        top: 4rem;
        background-color: #006AAF;
        border-radius: 20px;
        box-shadow: 5px 0 2px black;
    }
   
    
    th{
        color: black;
    }
    tr td{
        color: black;
    }
  

